@tailwind base;
@tailwind components;
@tailwind utilities;

/* Base styling */
body {
  @apply bg-metavanta-black text-metavanta-white font-sans;
}

/* Glitch animation */
@keyframes glitch {
  100% {
    text-shadow: 2px 2px #ff005a, -2px -2px #00f7ff;
  }
}

.animate-glitch {
  animation: glitch 0.3s steps(2, jump-none) infinite;
}

